#sidebar-toggle {
  color:rgba(black, .1);
}

html.html--sidebar-left.html--sidebar__collapsed {
  .page {
    padding-left: 0;

    .navbar-main.navbar-main__fixed {
      left: 0;
      #sidebar-toggle {
        color:rgba(black, .8);
      }
    }
    .sidebar {
      transform: scaleX(0);
    }
  }
}

.sidebar--header #user-info {
  top:.7em;
  left:1em;
}

.app-component-leave {
  opacity: 1;
  transition: opacity .5s ease-in;
}

.app-component-leave.app-component-leave-active {
  opacity:0;
}

.app-component-appear {
  opacity:.01;
}
.app-component-appear.app-component-appear-active {
  opacity: 1;
  transition: opacity .5s ease-in;
}



